.project-image {
  width: 75%;
  height: auto;
}

.project-detail {
  padding-top: 1.5rem;
}

@media only screen and (max-width: 600px) {
  .project-detail {
    margin-left: 1rem;
  }
}

@media only screen and (min-width: 600px) {
  .project-detail {
    margin-left: 6rem;
  }
}

svg {
  height: 1.5em;
  width: 1.5em;
  padding-right: 3px;
}

.project-links {
  font-size: 1.25rem;
  text-align: center;
}
